
import {
  ref, reactive, inject, defineComponent,
} from 'vue';
import { useRoute } from 'vue-router';

import { useStore } from '@/store';
import fetchAPI from '@/api/fetch';
import utils from '@/utils';

export default defineComponent({
  setup() {
    const route = useRoute();
    const store = useStore();
    const { fetchComicByID, fetchNovelByID } = fetchAPI();
    const { formatDate, simpleFormatDate } = utils();
    const load = ref(true);
    const type = ref('');
    const id = ref('');
    const entry = reactive({});
    const withFootHeight = inject('withFootHeight');

    return {
      session: store.getters['auth/getSession'],
      routeType: route.params.type,
      routeId: route.params.id,
      withFootHeight,
      load,
      type,
      id,
      entry,
      fetchComicByID,
      fetchNovelByID,
      formatDate,
      simpleFormatDate,
    };
  },
  /* eslint-disable no-param-reassign */
  beforeRouteEnter(to: any, from: any, next: any) {
    next(async (vm: any) => {
      const {
        session, routeType, routeId, id,
        fetchNovelByID, fetchComicByID,
      } = vm;
      vm.type = routeType;
      vm.id = routeId;
      if (routeType === 'novel') {
        vm.entry = await fetchNovelByID(session, routeId);
      } else if (routeType === 'comic') {
        vm.entry = await fetchComicByID(session, routeId);
      } else {
        return false;
      }
      vm.load = false;
      return true;
    });
  },
  /* eslint-enable no-param-reassign */
});
